.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(33, 63, 199, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.content {
    position: relative;
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    .bi-x {
        position: absolute;
        right: 20px;
        font-size: 20pt;
        cursor: pointer;
    }
}

@media (max-width: 700px) {
    .bi-x {
        display: none;
    }
}
